  ourChallengeBody: {
    ar:
      "            .                            .                                                               .",
    en:
      "The educational sector has recently witnessed a shift to online learning and digital courses. While many courses are available at the college level or higher, not many sessions are provided for school students. With the emphasis on the final years of high school and the critical importance of them for any young student preparing to take on the first steps of directing their career life, we came up with a quick, efficient solution. We established a platform that offers school students a variety of virtual courses that they can attend from the comfort of their homes, allowing them to benefit in their most comfortable setting.",
  },
  darislyBody: {
    ar:
      "        (    )          .                             . \n\n                     .                    .           .",
    en:
      "Darisly is an online platform that provides high school students in American, British, and Egyptian education system with a venue to attend online courses provided by different teachers. Instead of having to leave their homes to attend sessions, students can gain knowledge from the comfort of their homes and thus, minimal disruption to their school year pace. The platform is highly representative of courses given on a face-to-face basis; students have attendance, announcements, quizzes, homework, and assignments. At the launching phases, the platform focuses on Thanawiyya Amma students; courses uploaded in the beginning will be targeted towards them. However, courses for other curriculums will be added soon after.",
  },
  ourMission: {
    ar: " ",
    en: "Our Mission",
  },
  ourMissionBody: {
    ar:
      "                    .                                .",
    en:
      "We aim to become the leading online platform for high school students in Egypt and later in the Middle East. By utilizing technology and our technical team experience and understanding of the Egyptian educational environment needs to provide students with the best solutions to help them advance their high school education process.",
  },
