    en:
      "Darisly Group may disclose any information we have about you (including your identity) if we determine that such disclosure is necessary for connection with any investigation or complaint regarding your use of the Website, or to identify, contact or bring legal action against someone who may be causing injury to or interference with (either intentionally or unintentionally) Darisly Groups rights or property, or the rights or property of visitors to or users of the Website, including Darislys customers. Darisly Group reserves the right at all times to disclose any information that Darisly deems necessary to comply with any applicable law, regulation, legal process, or governmental request. Darisly Group also may disclose your information when Darisly Group determines that applicable law requires or permits such disclosure, including exchanging information with other companies and organizations for fraud protection purposes.\n\nYou agree that Darisly Group may, in its sole discretion and without prior notice, terminate your access to the Website and/or block your future access to the Website if we determine that you have violated these Terms of Use or other agreements or guidelines which may be associated with your use of the Website. You also agree that any violation by you of these Terms of Use will constitute an unlawful and unfair business practice, and will cause irreparable harm to Darisly Group, and you consent to Darisly Group obtaining any injunctive or equitable relief that Darisly Group deems necessary or appropriate in such circumstances. These remedies are in addition to any other remedies Darisly Group may have at law or in equity.\n\nYou agree that Darisly Group may, in its sole discretion and without prior notice, terminate your access to the Website, for cause, which includes (but is not limited to) (1) requests by law enforcement or other government agencies, (2) a request by you (self-initiated account deletions), (3) discontinuance or material modification of the Website or any service offered on or through the Website, or (4) unexpected technical issues or problems.\n\nIf Darisly Group does take any legal action against you as a result of your violation of these Terms of Use, Darisly Group will be entitled to recover from you, and you agree to pay, all reasonable attorneys fees and costs of such action, in addition to any other relief granted to Darisly Group. You agree that Darisly Group will not be liable to you or to any third party for termination of your access to the Website as a result of any violation of these Terms of Use.",
